/* GENERAL

*****************************************/
html,
body {
  height: 100%;
}
.main {
  position: relative;
  height: 100%;
}
.overmap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9;
  pointer-events: none;
}
.overmask2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transition: opacity 200ms ease-in-out;
  transition: opacity 200ms ease-in-out;
  pointer-events: none;
  z-index: 99;
  opacity: 0;
  background: rgb(68, 68, 68);
}
.overmask2.active {
  opacity: 0.8;
  pointer-events: all;
}
/* SIDEBARS

*****************************************/
.sidebar {
  position: absolute;
  top: 0;
  background: #e9efeb;
  height: 100%;
  width: 50%;
  z-index: 999;
  pointer-events: all;
  max-width: 400px;
}
.sidebar.left {
  left: -50%;
  -webkit-transition: left 200ms ease-in-out;
  transition: left 200ms ease-in-out;
  max-width: 320px;
}
.sidebar.right {
  right: -80%;
  width: 80%;
  -webkit-transition: right 200ms ease-in-out;
  transition: right 200ms ease-in-out;
  overflow-y: scroll;
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
  max-width: 700px;
}
.sidebar.right::-webkit-scrollbar {
  width: 0 !important;
}
.sidebar.left.active {
  left: 0;
}
.sidebar.right.active {
  right: 0;
}
.sidebar .sidebar-wrap {
  padding: 50px 20px;
}
.sidebar .portadas {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.sidebar .portadas>div {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  margin-right: 5px;
  margin-bottom: 5px;
}
.sidebar .portadas img {
  width: 100%;
}
@media only screen and (min-width: 870px) {
.right .sidebar-wrap {
    padding: 60px;
    right: calc(-50% - 40px);
}
}
@media only screen and (min-width: 1020px) {
.sidebar .portadas {
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
}
}
/* OVERCONTENT

*****************************************/
.content {
  position: absolute;
  top: 0;
  width: 50%;
  height: 100%;
  overflow: hidden;
}
.content.left {
  left: 0;
}
.content.right {
  right: 0;
}
.content .content-wrap {
  padding: 20px;
}
.content .menu {
  pointer-events: all;
  margin: 100px 20px 0;
  display: inline-block;
  font-weight: 700;
  font-size: 20px;
  cursor: pointer;
  -webkit-transition: opacity 350ms ease;
  transition: opacity 350ms ease;
}
.content .menu:hover {
  opacity: 0.6;
}
.content.right .menu-wrap {
  text-align: right;
}
/* Content main */
.content.main {
  width: 80%;
  height: 80%;
  left: 10%;
  top: 100%;
  z-index: 99;
  background: #e9efeb;
  pointer-events: all;
  -webkit-transition: top 200ms ease-in-out;
  transition: top 200ms ease-in-out;
}
.content.main.active {
  top: 10%;
}
.content.main .close {
  width: 20px;
  padding: 15px;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  -webkit-transition: opacity 350ms ease;
  transition: opacity 350ms ease;
}
.content.main .close:hover {
  opacity: 0.5;
}
.main-wrap {
  padding: 20px;
  height: calc(100% - 40px);
  overflow-y: scroll;
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: none;
}
.main-wrap::-webkit-scrollbar {
  width: 0 !important;
}
.main-thumb {
  width: 80%;
  max-width: 400px;
  height: auto;
  display: block;
  margin: 0 auto 20px;
}
.main-thumb {
  width: 100%;
  height: auto;
}
.main-title {
  text-align: center;
}
.main-body {
}
/* Ensigns */
.ensigns {
  position: absolute;
  top: 0;
  width: 100%;
  overflow: hidden;
}
.ensigns a {
  pointer-events: all;
  max-width: 260px;
  margin: 0 auto;
  display: block;
  width: 100%;
}
.ensigns img {
}
/* Logos */
.logos {
  position: absolute;
  bottom: 0;
  right: 0;
  text-align: center;
}
.logos a {
  pointer-events: all;
}
.logos img {
  width: 100px;
  height: auto;
}
/* Filters */
.filter-list {
}
.filter-item {
  padding: 10px 0;
  cursor: pointer;
  position: relative;
}
.filter-item.inactive .filter-element {
  opacity: 0.4;
}
.filter-legend {
  display: none;
  position: absolute;
  left: calc(100% + 30px);
  background: #212121;
  color: white;
  padding: 20px;
  width: calc(100% - 20px);
  top: -7px;
  border-left-width: 4px;
  border-left-style: solid;
}
.filter-item:hover .filter-legend {
  display: block;
}
.filter-element {
  -webkit-transition: opacity 350ms ease;
  transition: opacity 350ms ease;
}
.filter-element:hover {
  opacity: 0.3;
}
.filter-item.inactive .filter-element:hover {
  opacity: 0.8;
}
.filter-item h3 {
  margin-top: 0;
}
.filter-item img {
  width: 100%;
}
.filter-item p:last-child {
  margin-bottom: 0;
}
.filter-item .subtitle {
}
.filter-item .illustred {
  margin-top: 5px;
  font-size: smaller;
  text-align: right;
}
.filter-item .text {
  display: none;
}
.filter-item svg {
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  -webkit-transition: opacity 350ms ease;
  transition: opacity 350ms ease;
}
.filter-item.inactive svg {
  opacity: 0;
}
.filter-item.inactive .filter-element:hover svg {
  opacity: 0.8;
}
.filter-item span {
  vertical-align: middle;
}
@media only screen and (min-width: 600px) {
.content .menu {
    margin-top: 20px;
}
}
@media only screen and (min-width: 870px) {
.content.main {
    width: 800px;
    left: calc(50% - 400px);
    height: auto;
}
.main-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 40px;
}
.main-thumb {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 300px;
            flex: 0 0 300px;
}
.main-body {
    -webkit-box-flex: 1;
