.filter-legend h3 {
    margin-bottom: 0;
}
.filter-legend img {
    width: 200px;
    float: left;
    margin: 0 20px 10px 0;
}
.filter-item .subtitle {
    margin-bottom: 13px;
}
.filter-item .illustred {
    text-align: left;
    margin-bottom: 5px;
